import React, { useState } from 'react';
import './Scss/Projects.scss';
import Img1 from '../images/image1.jpg';

const ProjectOverview = () => {

    const [modal, setModal] = useState(false)

    const showModal = () => {
        setModal(true)
    }

    const [create, setCreate] = useState(false)

    const showForm = () => {
        setCreate(true)
    }

    return (
        <div className="Project-overview">
             <div className='wrapper'>
                <div className={`modal-contr d-flex align-items-center justify-content-center ${!modal && "d-none"}`}>
                    <div className="card rounded">
                        <div className="card-header d-flex align-items-center justify-content-between">
                            <span className='d-flex align-items-center justify-content-center'>
                                <i className='fa fa-globe mx-3'></i>
                                <p className='mt-'><b>Share to web</b> <br/> <p>Publish and share link with anyone</p></p>
                            </span>
                        </div>
                        <div className="card-body">
                            <span className="d-flex justify-content-between">
                                <p>Anyone with this link can view</p>
                                <a className='text-decoration-none text-primary h6'>Settings</a>
                            </span>
                            <input className='form-control rounded' type="text" value="https://sfaralijumanov" />
                        </div>
                        <div className='card-footer d-flex justify-content-between align-items-center'>
                            <a href='#' className='text-decoration-none text-primary h6'><i className='fa fa-clipboard'></i> Copy link</a>
                            <span>
                                <button onClick={() => setModal(false)} className='btn close mx-3'>Close</button>
                                <button className='btn share'>Share file</button>
                            </span>
                        </div>
                    </div>
                </div>
                <div className={`create-contr d-flex justify-content-end ${!create && "d-none"}`}>
                    <div className="card rounded">
                        <div className="card-header d-flex align-items-center justify-content-between">
                            <h5>Create a new project</h5>
                            <i className='fa fa-times' onClick={() => setCreate(false)}></i>
                        </div>
                        <div className="card-body">
                            <div className='form text-secondary'>
                                <label className='form-label text-dark' htmlFor='name'>Name</label>
                                <input className='form-control mb-3' type="text" placeholder='Project name' id='name' />
                                <p>Make it unique.</p>
                                <label className='form-label text-dark' htmlFor='desc'>Description</label>
                                <textarea placeholder='Project description' id='desc' className='form-control mb-3' rows="3"></textarea>
                                <p>Make it unique.</p>
                                <div className="form-check mt-4">
                                    <input type="radio" className='form-check-input' name="radio" id='only' checked />
                                    <label className='form-check-label text-dark' htmlFor='only'>
                                        Private
                                    </label>
                                </div>
                                <div className="form-check mt-4">
                                    <input type="radio" className='form-check-input' name="radio" id='public' />
                                    <label className='form-check-label text-dark' htmlFor='public'>
                                        Public
                                    </label>
                                </div>
                                <label className='form-label text-dark mt-4' htmlFor='client'>Client</label>
                                <input type="text" className="form-control" placeholder='Type client name' />
                                <label htmFor="date" className='form-label text-dark mt-4'>End date</label>
                                <input className='form-control w-50' id="date" type="date" />
                            </div>
                        </div>
                        <div className='card-footer alert-secondary d-flex justify-content-end align-items-center'>                            
                            <span>
                                <button onClick={() => setCreate(false)} className='btn close mx-3'>Close</button>
                                <button className='btn save'>Save</button>
                            </span>
                        </div>
                    </div>
                </div>
                <div className='c align-items-center justify-content-between'>
                    <h3>Projects</h3>
                    <span className="d-flex align-items-center">
                        <button className='btn rounded share mx-3' onClick={() => showModal()}><i className='fa fa-users'></i> Share</button>
                        <button className='btn rounded create' onClick={() => showForm()}><i className='fa fa-plus-square-o'></i> Create</button>
                    </span>
                </div>
                <nav className='navbar navbar-expand-md mt-3'>
                    <ul className="navbar-na">
                        <li className='nav-item'><a className="nav-link text-dark h5" href="#">View all</a></li>
                        <li className='nav-item'><a className="nav-link text-dark h5" href="#">Most recent</a></li>
                        <li className='nav-item'><a className="nav-link text-dark h5" href="#">Popular</a></li>
                    </ul>
                </nav>
                <div className="row c-1">
                    <div className='col-md-8 mt-4'>
                        <div className='orders shadow'>
                            <div className='col-md-12 d-flex justify-content-between'>
                                <h5>Orders</h5>
                                <button className='btn rounded'>Export</button>
                            </div>
                        </div>
                    </div>
                    <div className='col-md-4 mt-4'>
                        <div className='perfor shadow'>
                            <div className='col-md-12 d-flex justify-content-between'>
                                <h5>Top Performance</h5>
                                <a href="#" className='text-primary h6 text-decoration-none'>See all</a>
                            </div>
                            <div className='col-md-12 text-secondary mt-3 border rounded d-flex justify-content-between align-items-center'>
                                <span className='d-flex prof'>
                                    <img src={Img1} />
                                    <p><b className='text-dark'>Safarali Jumanov</b> <br/> Web Developer</p>
                                </span>
                                <i className='fa fa-ellipsis-v'></i>
                            </div>
                            <div className='col-md-12 text-secondary mt-3 border rounded d-flex justify-content-between align-items-center'>
                                <span className='d-flex prof'>
                                    <img src={Img1} />
                                    <p><b className='text-dark'>Safarali Jumanov</b> <br/> Web Developer</p>
                                </span>
                                <i className='fa fa-ellipsis-v'></i>
                            </div>
                            <div className='col-md-12 text-secondary mt-3 border rounded d-flex justify-content-between align-items-center'>
                                <span className='d-flex prof'>
                                    <img src={Img1} />
                                    <p><b className='text-dark'>Safarali Jumanov</b> <br/> Web Developer</p>
                                </span>
                                <i className='fa fa-ellipsis-v'></i>
                            </div>
                        </div>                        
                    </div>
                </div>
                <div className="row c-2">
                    <div className="col-md-4 mt-4">
                        <div className='budget shadow text-secondary'>
                            <span className='d-flex justify-content-between align-items-center'>
                                <h6>Budget <br /> <h3 className='text-dark mt-2'>$750.90</h3></h6>
                                <i className='fa fa-credit-card'></i>
                            </span>
                            <span className='d-flex align-items-center'>
                                <i className='fa fa-arrow-up'> 30%</i>
                                <p className='mt-2 mx-3'>Since last month</p>
                            </span>
                        </div>
                    </div>
                    <div className="col-md-4 mt-4">
                        <div className='new-projects shadow text-secondary'>
                            <span className='d-flex justify-content-between align-items-center'>
                                <h6>New projects <br /> <h3 className='text-dark mt-2'>215</h3></h6>
                                <i className='fa fa-users'></i>
                            </span>
                            <span className='d-flex align-items-center'>
                                <i className='fa fa-arrow-up'> 23%</i>
                                <p className='mt-2 mx-3'>Since last week</p>
                            </span>
                        </div>
                    </div>
                    <div className="col-md-4 mt-4">
                        <div className='total-hours shadow text-secondary'>
                            <span className='d-flex justify-content-between align-items-center'>
                                <h6>Total hours <br /> <h3 className='text-dark mt-2'>1.400</h3></h6>
                                <i className='fa fa-clock-o'></i>
                            </span>
                            <span className='d-flex align-items-center'>
                                <i className='fa fa-arrow-down'> -10%</i>
                                <p className='mt-2 mx-3'>Since last month</p>
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ProjectOverview;