import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import './Scss/Navbar.scss';
import { ListAlt, InsertDriveFile } from '@material-ui/icons';

const Navbar = () => {

    const [showdrop, setShowdrop] = useState(true)
    const [showdrop2, setShowdrop2] = useState(true)
    const [showdrop3, setShowdrop3] = useState(true)
    const [showdrop4, setShowdrop4] = useState(true)
    const [showdrop5, setShowdrop5] = useState(true)
    const [showdrop6, setShowdrop6] = useState(true)
    const [showdrop7, setShowdrop7] = useState(true)

    const handleShow = () => {
        if(showdrop){
            setShowdrop(false)
        }else{
            setShowdrop(true)
        }
    }
    const handleShow2 = () => {
        if(showdrop2){
            setShowdrop2(false)
        }else{
            setShowdrop2(true)
        }
    }
    const handleShow3 = () => {
        if(showdrop3){
            setShowdrop3(false)
        }else{
            setShowdrop3(true)
        }
    }
    const handleShow4 = () => {
        if(showdrop4){
            setShowdrop4(false)
        }else{
            setShowdrop4(true)
        }
    }
    const handleShow5 = () => {
        if(showdrop5){
            setShowdrop5(false)
        }else{
            setShowdrop5(true)
        }
    }
    const handleShow6 = () => {
        if(showdrop6){
            setShowdrop6(false)
        }else{
            setShowdrop6(true)
        }
    }
    const handleShow7 = () => {
        if(showdrop7){
            setShowdrop7(false)
        }else{
            setShowdrop7(true)
        }
    }

    const [showNavbar, setShowNavbar] = useState(false)

    return (
        <>
        <div className={`Navbar ${showNavbar && "show-navbar"}`}>
            <i className="fa fa-times" onClick={() => setShowNavbar(false)}></i>
            <nav className='navba navbar-expand-md'>
                <a className='navbar-brand'>Clever</a>
                <ul className='navbar-na'>
                    <li className='nav-item'>                        
                        <a className='nav-link d-flex justify-content-between align-items-center' onClick={() => handleShow()} role="button" id='drop1' data-bs-toggle='dropdown' aria-expanded="false">
                            <span><i className="fa fa-briefcase mx-3"></i> Projects</span> <i className={`fa fa-angle-right ${!showdrop && "d-none"}`}></i> <i className={`fa fa-angle-down ${showdrop && "d-none"}`}></i>
                        </a>
                        <ul className={`dropdown-men ${showdrop && "d-none"}`} aria-labelledby='drop1'>
                            <li><NavLink className="dropdown-item" to='/overpro'>Overview</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/grid'>Grid View</NavLink></li>
                            {/* <li><NavLink className="dropdown-item" to='/table'>Table View</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/detail'>Details</NavLink></li> */}
                            <li><NavLink className="dropdown-item" to='/createpro'>Create Project</NavLink></li>
                        </ul>                        
                    </li>
                    <li className='nav-item'>                        
                        <a className='nav-link d-flex justify-content-between align-items-center' onClick={() => handleShow5()} role="button" id='drop5' data-bs-toggle>
                            <span><i className="fa fa-user-circle mx-3"></i> User</span> <i className={`fa fa-angle-right ${!showdrop5 && "d-none"}`}></i> <i className={`fa fa-angle-down ${showdrop5 && "d-none"}`}></i>
                        </a>
                        <ul className={`dropdown-men ${showdrop5 && "d-none"}`} aria-labelledby='drop5'>
                            <li><NavLink className="dropdown-item" to='/profile'>Profile</NavLink></li>                            
                            <li><NavLink className="dropdown-item" to='/tableuser'>Table View</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/permissions'>Permissions</NavLink></li>                            
                        </ul>                        
                    </li>
                    <li className='nav-item'>                        
                        <a className='nav-link d-flex justify-content-between align-items-center' onClick={() => handleShow2()} role="button" id='drop2' data-bs-toggle>
                            <span><i className="fa fa-tasks mx-3"></i> Tasks</span> <i className={`fa fa-angle-right ${!showdrop2 && "d-none"}`}></i> <i className={`fa fa-angle-down ${showdrop2 && "d-none"}`}></i>
                        </a>
                        <ul className={`dropdown-men ${showdrop2 && "d-none"}`} aria-labelledby='drop2'>
                            <li><NavLink className="dropdown-item" to='/overtask'>Overview</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/list'>List View</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/detailview'>Details View</NavLink></li>
                            {/* <li><NavLink className="dropdown-item" to='/board'>Board View</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/createtask'>Create Task</NavLink></li> */}
                        </ul>                        
                    </li>
                    <li className='nav-item'>                        
                        <a className='nav-link d-flex justify-content-between align-items-center' onClick={() => handleShow3()} role="button" id='drop3' data-bs-toggle>
                            <span><i className="fa fa-file-text-o mx-3"></i> Files</span> <i className={`fa fa-angle-right ${!showdrop3 && "d-none"}`}></i> <i className={`fa fa-angle-down ${showdrop3 && "d-none"}`}></i>
                        </a>
                        <ul className={`dropdown-men ${showdrop3 && "d-none"}`} aria-labelledby='drop3'>
                            <li><NavLink className="dropdown-item" to='/overfile'>Overview</NavLink></li>                            
                            <li><NavLink className="dropdown-item" to='/tablefile'>Table View</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/mediafile'>Media Gallery</NavLink></li>                            
                        </ul>                        
                    </li>
                    <li className='nav-item'>                        
                        <a className='nav-link d-flex justify-content-between align-items-center' onClick={() => handleShow4()} role="button" id='drop4' data-bs-toggle>
                            <span><i className="fa fa-file-code-o mx-3"></i> Integrations</span> <i className={`fa fa-angle-right ${!showdrop4 && "d-none"}`}></i> <i className={`fa fa-angle-down ${showdrop4 && "d-none"}`}></i>
                        </a>
                        <ul className={`dropdown-men ${showdrop4 && "d-none"}`} aria-labelledby='drop4'>
                            <li><NavLink className="dropdown-item" to='/applications'>Applications</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/m-apps'>Manage Apps</NavLink></li>                            
                        </ul>                        
                    </li>                    
                    <li className='nav-item'>                        
                        <a className='nav-link d-flex justify-content-between align-items-center' onClick={() => handleShow6()} role="button" id='drop6' data-bs-toggle>
                            <span><i className="fa fa-cog mx-3"></i> Settings</span> <i className={`fa fa-angle-right ${!showdrop6 && "d-none"}`}></i> <i className={`fa fa-angle-down ${showdrop6 && "d-none"}`}></i>
                        </a>
                        <ul className={`dropdown-men ${showdrop6 && "d-none"}`} aria-labelledby='drop6'>
                            <li><NavLink className="dropdown-item" to='/general'>General</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/security'>Security</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/team'>Team</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/billing'>Billing</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/notifications'>Notifications</NavLink></li>
                        </ul>                        
                    </li>
                    <li className='nav-item'>                        
                        <a className='nav-link d-flex justify-content-between align-items-center' onClick={() => handleShow7()} role="button" id='drop7' data-bs-toggle>
                            <span><i className="fa fa-id-badge mx-3"></i> Authentication</span> <i className={`fa fa-angle-right ${!showdrop7 && "d-none"}`}></i> <i className={`fa fa-angle-down ${showdrop7 && "d-none"}`}></i>
                        </a>
                        <ul className={`dropdown-men ${showdrop7 && "d-none"}`} aria-labelledby='drop7'>
                            <li><NavLink className="dropdown-item" to='/basiclogin'>Basic Login</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/basicregister'>Basic Register</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/basicrecover'>Basic Recover</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/sidelogin'>Side Login</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/sideregister'>Side Register</NavLink></li>
                            <li><NavLink className="dropdown-item" to='/siderecover'>Side Recover</NavLink></li>
                        </ul>                        
                    </li>
                </ul>
            </nav>
        </div>
        <div className="Navbar1">
            <nav className='navbar d-flex justify-content-between align-items-center navbar-expand-md bg-white shadow'>
                <a className='navbar-brand' onClick={() => setShowNavbar(true)}><i className='fa fa-bars'></i></a>
                <a className='navbar-brand h4' hraef="#">Clever</a>
            </nav>
        </div>
        </>
    );
};

export default Navbar;