import React from 'react';
import { Route, Switch } from 'react-router-dom';
import GridView from './Routes/Projects/GridView';
import ProjectOverview from './Routes/Projects/ProjectOverview';
import TableView from './Routes/Projects/TableView';
import CreateProject from './Routes/Projects/CreteProject';
import Details from './Routes/Projects/Details';
import TaskOverview from './Routes/Tasks/TaskOverview';
import ListView from './Routes/Tasks/ListView';
import DetailView from './Routes/Tasks/DetailView';
import Board from './Routes/Tasks/Board';
import CreateTask from './Routes/Tasks/CreateTask'; 
import Profile from './Routes/User/Profile';
import Home from './Home/Home';

const AppRouter = () => {
    return (
        <div className='App-router'>
            <Switch>
                <Route path="/" exact component={Home} />
                <Route path="/overpro" component={ProjectOverview} />
                <Route path="/grid" component={GridView} />
                <Route path="/table" component={TableView} />
                <Route path="/detail" component={Details} />
                <Route path="/createpro" component={CreateProject} /> 
                <Route path="/overtask" component={TaskOverview} /> 
                <Route path="/list" component={ListView} /> 
                <Route path="/detailview" component={DetailView} /> 
                <Route path="/board" component={Board} /> 
                <Route path="/createtask" component={CreateTask} />  
                <Route path="/profile" component={Profile} />           
            </Switch>
        </div>
    );
};

export default AppRouter;