import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import Img1 from '../images/image9.jpg';
import Img2 from '../images/image6.jpg';
import './Scss/User.scss';

const Profile = () => {

    let dataProfile = [
        {
            id: 1, firstName: "Safarali", img: Img2, lastName: "Jumanov", 
            profs: [{prof: "UI/UX designer"}, {prof: "Frontend developer"}],
            location: "Uzbekistan, Syrdarya",
            email: "12345@gmail.com",
            phone: "+12345678",
            facebook: "ali/facebook.com",
            github: "alijumanov/github.com",
            biography: "Lorem ipsum, dolor sit amet consectetur adipisicing elit. Laudantium quo quod nulla distinctio! Ex commodi dolores sint recusandae maxime quisquam praesentium quia porro dicta, tempora nostrum voluptas eaque amet repudiandae eius in ullam libero aperiam placeat tempore repellendus aspernatur rem dolore. Inventore qui nemo dolores accusamus amet libero optio natus."
        }
    ]

    const [dataPro, setDataPro] = useState(dataProfile)
    const [dataPro1, setDataPro1] = useState(dataProfile.map((data) => data.firstName))
    const [dataPro2, setDataPro2] = useState(dataProfile.map((data) => data.lastName))
    const [dataPro3, setDataPro3] = useState(dataProfile.map((data) => data.biography))
    const [dataPro4, setDataPro4] = useState(dataProfile.map((data) => data.location))
    const [dataPro5, setDataPro5] = useState(dataProfile.map((data) => data.email))
    const [dataPro6, setDataPro6] = useState(dataProfile.map((data) => data.phone))
    const [dataPro7, setDataPro7] = useState(dataProfile.map((data) => data.facebook))
    const [dataPro8, setDataPro8] = useState(dataProfile.map((data) => data.github))

    const [profile, setProfile] = useState(true);


    return (
        <div className="Profile pb-5">
            <div className='wrapper pb-5'>
                {dataPro.map((data) => {
                    return(
                        <div className='prof-bio'>
                            <div className={`contr-prof ${profile && "d-none"}`}>
                                <div className='col-md-6 edit-profile'> 
                                    <div className="row">
                                        <div className="col-md-6">
                                            <label htmlFor='firstname' className='form-label mt-3'>FirstName</label>
                                            <input type="text" value={data.firstName} id='firstname' className='form-control' placeholder='Firstname' />
                                        </div>
                                        <div className="col-md-6">
                                            <label htmlFor='lastname' className='form-label mt-3'>LastName</label>
                                            <input type="text" value={data.lastName} id='lastname' className='form-control' placeholder='Lastname' />
                                        </div>
                                    </div>                                    
                                    <div className='row border-top border-bottom mt-3'>
                                        <div className='col-md-12 input-group mt-3'>                                
                                            <input type="text" id="skill" className='form-control w-50' placeholder='Add your proffessions or skills' aria-describedby="addon" />
                                            <button className='btn add' id="addon">Add</button>
                                        </div>                                        
                                        <div className="d-flex pro px-4"> 
                                            {data.profs.map((prof) => {
                                                return(
                                                    <div className='proff col-md-2 mt-3 mb-3'>
                                                        <div className="text-center">
                                                            {prof.prof}
                                                        </div>                                
                                                    </div>
                                                )
                                            })}
                                        </div>
                                    </div>
                                    <label htmlFor='bio' className='form-label mt-3'>Biography</label>
                                    <textarea id='bio' value={data.biography} className='form-control' rows="4"></textarea>
                                    <label htmlFor='location' className='form-label mt-3'>Location</label>
                                    <input id='location' value={data.location} type="text" className='form-control' placeholder='Location' />
                                    <div className='row'>
                                        <div className='col-md-6 mt-3'>
                                            <label htmlFor='email' className='form-label'>Email</label>
                                            <input id='email' value={data.email} className='form-control' placeholder='Email' />
                                        </div>
                                        <div className='col-md-6 mt-3'>
                                            <label htmlFor='phone' className='form-label'>Phone number</label>
                                            <input id='phone' value={data.phone} className='form-control' placeholder='Phone number' />
                                        </div>
                                        <div className='col-md-6 mt-3'>
                                            <label htmlFor='facebook' className='form-label'>Facebook</label>
                                            <input id='facebook' value={data.facebook} className='form-control' placeholder='Facebook' />
                                        </div>
                                        <div className='col-md-6 mt-3'>
                                            <label htmlFor='github' className='form-label'>Github</label>
                                            <input id='github' value={data.github} className='form-control' placeholder='Github' />
                                        </div>
                                    </div>
                                    <div className="col-md-12 d-flex mt-3 justify-content-end">
                                        <button className='btn close mx-3' onClick={() => setProfile(true)}>Close</button>
                                        <button className='btn save'>Save</button>
                                    </div>
                                </div>
                            </div>
                            <div className='bio row'>
                                <div className="col-md-3 mt-3 p-2">
                                    <img src={data.img} />
                                </div>  
                                <div className='col-md-4 row mt-3'>
                                    <div className="col-md-12">
                                        <h4>{data.firstName} {data.lastName}</h4>
                                    </div>
                                    {data.profs.map((prof) => {
                                        return(
                                            <span className='proff col-md-4 mt-3'>
                                                <div className="text-center">
                                                    {prof.prof}
                                                </div>                                
                                            </span>
                                        )
                                    })}
                                </div>  
                                <div className='col-md-5 mt-3'>
                                    <button className='btn notificarion'><i className='fa fa-envelope'></i></button>
                                    <button className='btn notificarion mx-3'><i className='fa fa-bell'></i></button>
                                    <button className='btn edit' onClick={() => setProfile(false)}>Edit profile</button>
                                </div>
                            </div>
                            <hr className='text-secondary mt-5 mb-4'/>
                            <div className="row">
                                <div className='col-md-8 mt-3 px-3'>
                                    <div className='bio-text shadow'>
                                        <h5>Biography</h5> <br/>
                                        <p>{data.biography}</p>
                                    </div>
                                </div>
                                <div className='col-md-4 px-3 mt-3'>
                                    <div className='location shadow'>
                                        <h5>Location</h5>
                                        <p><i className='fa fa-map-marker'></i> {data.location}</p>
                                    </div>
                                    <div className="social shadow mt-3">
                                        <h5>Contacts</h5>
                                        <i className="fa fa-envelope"> {data.email}</i> <br/>
                                        <i className="fa fa-phone"> {data.phone}</i> <br/>
                                        <i className="fa fa-facebook-square"> {data.facebook}</i> <br/>
                                        <i className="fa fa-github"> {data.github}</i>
                                    </div>
                                </div>
                            </div>
                            <hr className='text-secondary mt-5 mb-4'/>
                            <div className='row'>
                                <div className='col-md-12'><h4>My files</h4></div>
                                <div className='col-md-3 mt-3 px-3'>
                                    <div className="file shadow">
                                        <img src={Img1} />
                                        <h5 className="mt-3">Portfolio Website</h5>
                                        <p>&#11088; &#11088; &#11088; &#10025; &#10025;</p>
                                    </div>
                                </div>
                                <div className='col-md-3 mt-3 px-3'>
                                    <div className="file shadow">
                                        <img src={Img1} />
                                        <h5 className="mt-3">Portfolio Website</h5>
                                        <p>&#11088; &#11088; &#11088; &#10025; &#10025;</p>
                                    </div>
                                </div>
                                <div className='col-md-3 mt-3 px-3'>
                                    <div className="file shadow">
                                        <img src={Img1} />
                                        <h5 className="mt-3">Portfolio Website</h5>
                                        <p>&#11088; &#11088; &#11088; &#10025; &#10025;</p>
                                    </div>
                                </div>
                                <div className='col-md-3 mt-3 px-3'>
                                    <div className="file shadow">
                                        <img src={Img1} />
                                        <h5 className="mt-3">Portfolio Website</h5>
                                        <p>&#11088; &#11088; &#11088; &#10025; &#10025;</p>
                                    </div>
                                </div>
                                <div className='col-md-3 mt-3 px-3'>
                                    <div className="file shadow">
                                        <img src={Img1} />
                                        <h5 className="mt-3">Portfolio Website</h5>
                                        <p>&#11088; &#11088; &#11088; &#10025; &#10025;</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                })}
            </div>
        </div>
    );
};

export default Profile;