import React from 'react';
import './Scss/Task.scss';

const DetailView = () => {
    return (
        <div className='Detail-view'>
             <div className="wrapper">
                <div className="form text-secondary">
                    <label className='form-label text-dark' htmlFor='name'>Name</label>
                    <input className='form-control mb-3' type="text" placeholder='Project name' id='name' />
                    <p>Make it unique.</p>
                    <label className='form-label text-dark mt-4' htmlFor='desc'>Description</label>
                    <textarea placeholder='Project description' id='desc' className='form-control mb-3' rows="3"></textarea>
                    <p>Make it unique.</p>
                    <hr className="mt-4 mb-4" />
                    <div className="form-check mt-4">
                        <input type="radio" className='form-check-input' name="radio" id='only' checked />
                        <label className='form-check-label text-dark' htmlFor='only'>
                            Private
                        </label>
                    </div>
                    <div className="form-check mt-4">
                        <input type="radio" className='form-check-input' name="radio" id='public' />
                        <label className='form-check-label text-dark' htmlFor='public'>
                            Public
                        </label>
                    </div>
                    <hr className="mt-4" />
                    <label className='form-label text-dark mt-4 mx-0' htmlFor='client'>Client</label>
                    <input type="text" className="form-control" placeholder='Type client name' />
                    <label htmFor="date" className='form-label text-dark mt-4'>End date</label>
                    <input className='form-control w-50' id="date" type="date" />
                    <div className='col-md-12 mt-5 d-flex justify-content-end'>
                        <button className="btn save">Save</button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default DetailView;